.modelContainer-title{
    width: 92%;
    height: 5%;
    margin: 6% 10px 4.5rem 4%;
    font-size: 50px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.14;
    letter-spacing: -1px;
    color: #373737;
}

@media only screen and (max-width: 750px) {
    .modelContainer-title{
        width: 92%;
        font-size: 37px;
    }
}